html, body{
    background-color:#FFD400;
    color: #32322C;
    font-family:sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.mybtn{
    background-color:none;
    top: 30%;
    border-radius: 20px;
    text-decoration: none;
    font-size: 20px;
    border: 2px solid;
    padding: 3px 20px;
}

.mybtn:hover{
    background-color: #FFD400;
    cursor: pointer;
}

.contain{
    top: 10%;
    width: 500px;    
    text-align: center;
    display: block;
    margin: 0 auto;
    margin-top: 70px;
    
    border-radius: 20px;
    padding: 20px;
    background-color:#3590F3;
}

